	module.exports = {"data":{"markdownRemark":{"html":"<p>Continuons notre lecture aux versets 29 et 30 de 1 Corinthiens 12 :</p>\n<blockquote>\n<p>« Tous sont-ils apôtres? Tous sont-ils prophètes? Tous sont-ils docteurs? Tous ont-ils le don des miracles? Tous ont-ils le don des guérisons? Tous parlent-ils en langues? Tous interprètent-ils? » <br /> <small>- 1 Corinthiens 12:29,30</small></p>\n</blockquote>\n<p>Paul pose une série de questions auxquelles il serait intéressant de répondre :</p>\n<ul>\n<li>Tous sont-ils apôtres? <strong>NON</strong></li>\n<li>Tous sont-ils prophètes? <strong>NON</strong></li>\n<li>Tous sont-ils docteurs? <strong>NON</strong></li>\n<li>Tous ont-ils le don des miracles? <strong>NON</strong></li>\n<li>Tous ont-ils le don des guérisons? <strong>NON</strong></li>\n<li>Tous parlent-ils en langues? <strong>NON</strong></li>\n<li>Tous interprètent ils? <strong>NON</strong></li>\n</ul>\n<p><strong>Conclusion :</strong> Tous ne peuvent avoir reçu le même don, y compris celui des langues !</p>\n<p>Comme nous l'avons vu précédemment, chaque membre reçoit un don quelconque selon le bon vouloir de L'Esprit-Saint, tous ne reçoivent pas le même don.</p>","frontmatter":{"title":"Est-il possible que tous les membres d'une église parlent en langue ?"}}},"pathContext":{"slug":"/est-il-possible-que-tous-les-membres-d-une-eglise-parlent-en-langue/"}}

